.player-card{
    width: 50%;
    height: 50vh;
    margin-left: 370px;
    background-color: #212121;
    background-image: url('https://m.media-amazon.com/images/I/813mZCjKSWL._AC_SL1500_.jpg?7hqt659brkp0g') !important;
    border-radius: 30px;
    -webkit-box-shadow: 13px 12px 40px -7px rgba(0,0,0,0.75);
-moz-box-shadow: 13px 12px 40px -7px rgba(0,0,0,0.75);
box-shadow: 13px 12px 40px -7px rgba(0,0,0,0.75);
margin-top: 60px;
   

}

.player-main{
    padding: 10px;
   
    
}

.control-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 180px;
    gap: 20px;
    position: absolute;
    margin-left: 130px;
}

.icons{
    -webkit-box-shadow: 10px 0px 55px 11px rgba(227,149,32,1);
    -moz-box-shadow: 10px 0px 55px 11px rgba(227,149,32,1);
    box-shadow: 10px 0px 55px 11px rgba(227,149,32,1);
    border-radius: 50%;
}


.icons:hover{
    color: #ff9800 !important;
    cursor:pointer;
}

.activesong-name{
    font-family: Copperplate, Papyrus, fantasy;
  left:35%;
  transform: translate(-50,0);
  display: flex;
    justify-content: center;
    position: absolute;
    margin-top:80px;
    font-color: black !important;
  
}

.activeartist-name{
    color: black;
    font-family: Copperplate, Papyrus, fantasy;
    left:35%;
    transform: translate(-50,0);
    display: flex;
      justify-content: center;
      position: absolute;
      margin-top:130px;

}

.podcast-name{
    text-align: center;
    font-size: 20px;
}

.podcast-container{
    text-align:center;
    margin-top: 30px;
    font-style:italic;
    background-color: #212121;
    color: black;
    padding: 5px;
    margin: 5px;
    cursor: pointer;
    width: 350px;;
    margin-left: 50px;
    height: 40px;
    position: relative;
    top: 50%;
    border-radius: 20px;
    
}

.podcast-container:hover{
    background-color: black;
}
